// import IndexPage from '../page/IndexPage'
// import HomeTab from '../page/index/HomeTab'
// import ShoppingTab from '../page/index/ShoppingTab'
// import MeTab from '../page/index/MeTab'
//
// import LotteryDetailPage from '../page/LotteryDetailPage'

const IndexPage = () => import('../page/IndexPage')
const HomeTab = () => import('../page/index/HomeTab')
const ShoppingTab = () => import('../page/index/ShoppingTab')
const MeTab = () => import('../page/index/MeTab')

const LotteryDetailPage = () => import('../page/LotteryDetailPage')
const UserLoginPage = () => import('../page/UserLoginPage')
const UserRegisterPage = () => import('../page/UserRegisterPage')
const UserResetPage = () => import('../page/UserResetPage')
const CartPage = () => import('../page/CartPage')
const OrderDetailPage = () => import('../page/OrderDetailPage')

export default [
    {
        name: "index",
        path: "/",
        component: IndexPage,
        children: [
            {
                name: "home",
                path: "home",
                component: HomeTab
            },
            {
                name: "shopping",
                path: "shopping",
                component: ShoppingTab
            },
            {
                name: "me",
                path: "me",
                component: MeTab
            }
        ]
    },
    {
        name: "lotteryDetail",
        path: "/lottery/detail",
        component: LotteryDetailPage
    },
    {
        name: "userLogin",
        path: "/user/login",
        component: UserLoginPage
    },
    {
        name: "userRegister",
        path: "/user/register",
        component: UserRegisterPage
    },
    {
        name: "userReset",
        path: "/user/reset",
        component: UserResetPage
    },
    {
        name: "cart",
        path: "/cart",
        component: CartPage
    },
    {
        name: "orderDetail",
        path: "/order/detail",
        component: OrderDetailPage
    }
]