import Vue from "vue"
import NET from '../../config/net/NET'

const state = {
    all: []
}

const getters = {
    cartCount: (state) => state.all.length,
    carts: (state) => state.all,
    cartsPayMoney: (state) => {
        let size = 0
        state.all.forEach(function (item) {
            size += parseInt(item.count)
        })
        return size * 2
    }
}

const actions = {
    loadCart({commit, getters}) {
        const uid = getters.user.uid

        const carts = Vue.ls.get(uid + "_carts", [])
        commit('setCarts', {uid, carts})
    },
    cartAdd({commit, getters}, {redBalls, blueBall, count}) {
        let red = ""
        let blue = blueBall

        redBalls.forEach(function (item, index) {
            red += item
            if (index != redBalls.length - 1) {
                red += ","
            }
        })

        const uid = getters.user.uid
        const data = {red, blue, count, uid}
        commit('cartAdd', data)
    },
    cartRemove({commit, getters}, cart) {
        const uid = getters.user.uid

        commit('cartRemove', {
            uid: uid,
            cart: cart
        })
    },
    cartSaveToOrder({commit, getters}, callback) {
        const uid = getters.user.uid
        const carts = getters.carts
        console.log(carts)

        const order = JSON.stringify(carts)
        console.log(order)

        NET.saveOrder(uid, order , data => {
            callback(data)

            commit('setCarts', {uid: uid, carts: []})
        })
    }
}

const mutations = {
    setCarts(state, value) {
        const uid = value.uid
        const carts = value.carts

        state.all = carts

        Vue.ls.set(uid + "_carts", state.all)
    },
    cartAdd(state, {red, blue, count, uid}) {
        state.all.push({red: red, blue: blue, count: parseInt(count)})

        Vue.ls.set(uid + "_carts", state.all)
    },
    cartRemove(state, data) {
        const cart = data.cart
        const uid = data.uid

        const index = state.all.indexOf(cart)
        state.all.splice(index, 1)

        Vue.ls.set(uid + "_carts", state.all)
    }
}

export default {
    state, getters, actions, mutations
}