const state = {
    selectedTab: 0,
    enableTab: true
}

const getters = {
    selectedTab: (state) => state.selectedTab,
    enableTab: (state) => state.enableTab
}

const actions = {
    selectTab({commit}, index) {
        commit('setSelectedTab', index)
    },
    enableTab({commit}, enable) {
        commit('enableTab', enable)
    }
}

const mutations = {
    setSelectedTab(state, index) {
        state.selectedTab = index
    },
    enableTab(state, enable) {
        state.enableTab = enable
    }
}

export default {
    state, getters, actions, mutations
}