import Vue from 'vue'
import NET from "../../config/net/NET";

const state = {
    user: null
}

const getters = {
    isLogin: (state) => {
        return state.user != null
    },
    user: (state) => state.user
}

const actions = {
    loadLocalUser({commit}) {
        const user = Vue.ls.get("user", null)
        commit('setUser', user)
    },
    login({commit}, {email, password}) {
        NET.login(email, password, data => {
            if (data.flag == true) {
                commit('setUser', data.data)

                Vue.ls.set("user", data.data)
            }
        })
    },
    logout({commit}) {
        Vue.ls.clear("user")
        commit('setUser', null)
    }
}

const mutations = {
    setUser(state, user) {
        state.user = user
    }
}

export default {
    state, getters, actions, mutations
}