<template>

    <div class="index-container">

        <div class="index-content">
            <transition :name="transitionName">
                <router-view></router-view>
            </transition>
        </div>

        <transition name="slide-down">
            <div class="index-footer" v-show="enableTab">
                <tab-bar :index="selectedTab" @onTabSelected="onTabSelected"></tab-bar>
            </div>
        </transition>
    </div>

</template>

<script>
    import TabBar from "../components/TabBar";

    import {mapGetters} from 'vuex'

    export default {
        name: "HomePage",
        components: {TabBar},
        data() {
            return {
                title: "黑马彩票",
                transitionName: 'slide-left'
            }
        },
        created() {
            const index = this.selectedTab
            if (index == 0) {
                this.$router.push("home")
            } else if (index == 1) {
                if (this.isLogin) {
                    this.$router.push("shopping")
                } else {
                    this.$router.push({name: "userLogin"})
                }
            } else if (index == 2) {
                if (this.isLogin) {
                    this.$router.push("me")
                } else {
                    this.$router.push({name: "userLogin"})
                }
            }
        },
        computed: {
            ...mapGetters({
                isLogin: 'isLogin',
                selectedTab: 'selectedTab',
                enableTab: 'enableTab'
            })
        },
        methods: {
            onTabSelected(index) {
                if (index == 0) {
                    this.$router.push("home")
                } else if (index == 1) {
                    if (this.isLogin) {
                        this.$router.push("shopping")
                    } else {
                        this.$router.push({name: "userLogin"})
                    }
                } else if (index == 2) {
                    if (this.isLogin) {
                        this.$router.push("me")
                    } else {
                        this.$router.push({name: "userLogin"})
                    }
                }
            }
        }
    }
</script>

<style scoped>

    .index-container {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
    }

    .index-content {
        width: 100%;
        height: 100%;
        position: relative;
    }

    .index-content > div {
        position: absolute;
        top: 0;
    }

    .index-footer {
        width: 100%;
        height: 4.5rem;
    }

</style>