import Vue from 'vue'
import VueRouter from 'vue-router'
import store from './store'
import Routes from './routes/router'
import App from './App.vue'
import 'vue-awesome/icons'

Vue.config.productionTip = false

Vue.use(VueRouter);

const router = new VueRouter({
    routes: Routes
})

new Vue({
    store,
    router,
    render: h => h(App)
}).$mount('#app')
