import NET from '../../config/net/NET'

const state = {
    data: null
}

const getters = {
    historyList: (state) => {
        if (state.data == null) return []
        return state.data.content
    }

}

const actions = {
    loadHistory({commit}) {
        NET.loadHistory(data => {
            commit('setHistories', data)
        })
    }
}

const mutations = {
    setHistories(state, data) {
        state.data = data
    }
}

export default {
    state, getters, actions, mutations
}