import NET from './net_config.js'

//加载历史信息
function loadHistory(callback) {
    const url = `/history`
    NET.info.get(url).then(response => callback(response.data))
}

//用户登录
function login(email, password, callback) {
    const url = "/login"
    const data = {
        email: email,
        password: password
    }
    NET.user.post(url, data).then(response => callback(response.data))
}

//创建订单
function saveOrder(uid, carts, callback) {
    const url = "/save"
    const data = {
        uid: uid,
        carts: carts
    }
    NET.order.post(url, data).then(response => callback(response.data))
}


export default {
    loadHistory,
    login,
    saveOrder
}