<template>
    <div class="tab-root">

        <div class="tab-item"
             :class="selectedIndex == 0 ? 'selected' :''"
             @click="clickItem(0)">
            <icon name="home" scale="1.8"></icon>
            <span>首页</span>
        </div>

        <div class="tab-item"
             :class="selectedIndex == 1 ? 'selected' :''"
             @click="clickItem(1)">
            <icon name="shopping-bag" scale="1.8"></icon>
            <span>选号</span>
        </div>

        <div class="tab-item"
             :class="selectedIndex == 2 ? 'selected' :''"
             @click="clickItem(2)">
            <icon name="user" scale="1.8"></icon>
            <span>我</span>
        </div>
        <div style="display: none">{{index}}</div>
    </div>
</template>

<script>
    import Icon from "vue-awesome/components/Icon";

    export default {
        name: "TabBar",
        components: {Icon},
        props: {
            index: {
                require: false,
                type: Number,
                default: 0
            }
        },
        data() {
            return {
                selectedIndex: 0
            }
        },
        watch: {
            index(newVal, oldVal) {
                this.selectedIndex = newVal
            }
        },
        mounted() {

        },
        methods: {
            clickItem(index) {
                this.selectedIndex = index

                this.$emit("onTabSelected", index)
            }
        }
    }
</script>

<style scoped>
    .tab-root {
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        position: relative;
        width: 100%;
        height: 5rem;
        border: .1rem none #33333333;
        border-top-style: solid;
        display: flex;
        align-items: center;
        justify-content: space-around;

        backdrop-filter: blur(10px);
    }

    .tab-item {
        width: 5rem;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        font-size: 1.2rem;
        color: black;
    }

    .tab-item.selected {
        color: red;
        transition: all .4s;
    }


</style>